function input(props){

    return (
        <div>
            <textarea
                id='editor'
                value={props.input}
                onChange={props.change}
            ># Why React? \r ## I certainly didn\'t need if for my 2-component 1-page program! \r ### I like React \r I *wanted* to use it **obviously!** \r ___  \r  `background-color: white` \r [links](https://gabester.tech) \r</textarea>
            <style jsx>{`
                #editor {
                    width: 800px;
                    height: 200px;
                    margin: auto;
                } 
            `}</style>
        </div>
    )
}

export default input;